<template>
  <div style="max-width: 100%;box-sizing: border-box;">
    <myHeader :isBack="true" backPath="/personInfo" title="我的信息"></myHeader>
    <div style="padding:.12rem 0;background:#f8f8f8;" class="mt4">
      <mt-cell title="姓名：" :value="myInfo.fname"></mt-cell>
      <mt-cell title="手机号：" :value="myInfo.ftelno"></mt-cell>
      <mt-cell title="猪舍地址：" :value="myInfo.cfpigpen"></mt-cell>
      <mt-cell title="开户时间：" :value="myInfo.fkhsj"></mt-cell>
      <mt-cell title="所属服务部：" :value="myInfo.servicedep"></mt-cell>
      <mt-cell title="管理员：" :value="myInfo.manager"></mt-cell>
      <mt-cell title="管理员电话：" :value="myInfo.fcell"></mt-cell>
      <mt-cell title="饲养状态：" :value="myInfo.cfraisestateText"></mt-cell>
      <mt-cell title="我的星级：" :value="myInfo.cflevel"></mt-cell>
      <mt-cell title="委托领取人姓名：" :value="myInfo.entrustName"></mt-cell>
      <mt-cell title="委托领取人电话：" :value="myInfo.entrustPhone"></mt-cell>
      <mt-cell title="委托领取人身份证号：" :value="myInfo.entrustIdcard"></mt-cell>
    </div>
  </div>
</template>
<script>
import myHeader from "@/components/Header";
import { getFeedPersonInfo } from "@/api/userCenter";
export default {
  name: "myInformation",
  components: {
    myHeader
  },
  data() {
    return {
      myInfo: {}
    };
  },
  created() {
    this._getFeedPersonInfo();
  },
  methods: {
    gotoRecord() {
      this.router.push("/");
    },
    _toNotNull(data) {
      return data ? data : "-";
    },
    _getFeedPersonInfo() {
      getFeedPersonInfo().then(
        res => {
          console.log(res.data);
          let feedPersonObj = res.data;
          this.myInfo = {
            fname: this._toNotNull(feedPersonObj.fname),
            ftelno: this._toNotNull(feedPersonObj.ftelno),
            cfpigpen: this._toNotNull(feedPersonObj.cfpigpen),
            fkhsj: this._toNotNull(feedPersonObj.fkhsj),
            servicedep: this._toNotNull(feedPersonObj.servicedep),
            manager: this._toNotNull(feedPersonObj.manager),
            fcell: this._toNotNull(feedPersonObj.fcell),
            cfraisestateText: this._toNotNull(feedPersonObj.cfraisestateText),
            cflevel: this._toNotNull(feedPersonObj.cflevel),
            entrustName: this._toNotNull(feedPersonObj.entrustName),
            entrustPhone: this._toNotNull(feedPersonObj.entrustPhone),
            entrustIdcard: this._toNotNull(feedPersonObj.entrustIdcard)
          };
        },
        err => {}
      );
    }
  }
};
</script>
<style lang='scss' scoped>
</style>



